import * as React from "react"

import Layout from "../components/layout"
import Seo from "../components/seo"
import logo from "../images/logo.png"
import influencerInfographic from "../images/influencer.png"
import merchantInfographic from "../images/merchant.png"
import { Grid, Typography, createTheme, ThemeProvider, AppBar, Toolbar, Button, Box, Popover } from '@mui/material'
import InstagramIcon from '@mui/icons-material/Instagram';

const theme = createTheme({
  palette: {
    primary: {
      main: '#704efb'
    }
  },
  typography: {
    fontFamily: [
      'Lato',
      'sans-serif'
    ].join(','),
    h1: {
      fontWeight: 700,
    },
    h2: {
      fontWeight: 400,
      fontSize: '1.5rem',
      '@media (min-width:600px)': {
        fontSize: '3.0rem',
      },
    },
    h3: {
      fontWeight: 300,
    },
  },
});


function IndexPage() {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);
  const id = open ? 'simple-popover' : undefined;

  return (
    <Layout>
      <ThemeProvider theme={theme}>
        <Box sx={{ flexGrow: 1 }}>
          <AppBar position="static">
            <Toolbar sx={{ bgcolor: '#EDEDED' }}>
              <img src={logo} width="100" height="100" style={{ margin: '1vh' }} alt="Curato Logo" />
              <Button aria-describedby={id} variant="contained" color="primary" sx={{ marginLeft: "auto" }} onClick={handleClick}>Waitlist</Button>
              <Popover
                id={id}
                open={open}
                anchorEl={anchorEl}
                onClose={handleClose}
                anchorOrigin={{
                  vertical: 'bottom',
                  horizontal: 'left',
                }}
              >
                <Box sx={{ p: 2 }}>
                  <Typography>I am a...</Typography>
                  <Button onClick={event =>  window.location.href='https://docs.google.com/forms/d/e/1FAIpQLSfqvLhOUjo1JO4sLUpDwhMo7mrw1ar7JiTkSKLGiHqvtiKoSQ/viewform?usp=sf_link'}>Merchant</Button>
                  <Button onClick={event =>  window.location.href='https://docs.google.com/forms/d/e/1FAIpQLSfIjni_hv9X0LbGilPF3hpgubcz7u5FyVmb4WhYmGQjwPkQ9w/viewform?usp=sf_link'}>Influencer</Button>
                  <Button onClick={event =>  window.location.href='https://docs.google.com/forms/d/e/1FAIpQLSdOP1Hsd-hiEwomWrMVb3RLFAdMXnCi9JkW4o91qcgl8E9Wxg/viewform?usp=sf_link'}>Agency</Button>
                </Box>
              </Popover>
              <Button>
                <InstagramIcon onClick={event =>  window.location.href='https://www.instagram.com/curatocommerce/'} />
              </Button>
            </Toolbar>
          </AppBar>
        </Box>
        <Grid container
          sx={{ flexDirection: { xs: "row" }, mt: 1 }}
          spacing={2}
          alignItems="center"
          style={{ height: '100vh' }}
        >
          <Grid item xs={12} style={{textAlign: "center"}}>
            <img src={influencerInfographic} alt="Influencer infographic" />
            <img src={merchantInfographic} alt="Merchant infographic" />            
          </Grid>
        </Grid>
      </ThemeProvider>
    </Layout>
  )
}

/**
 * Head export to define metadata for the page
 *
 * See: https://www.gatsbyjs.com/docs/reference/built-in-components/gatsby-head/
 */
export const Head = () => <Seo title="Home" />

export default IndexPage
